body {
  margin: 0;
  padding: 0;
  font-family: 'Quicksand', sans-serif;
}

.background {
  background-color: #eff5ff;
  padding: 1em 1em 4em 1em;
}

.card {
  background-color: #ffffff;
  color: #000000;
  position: relative;
  padding-bottom: 5px;
  text-align: center;
  border: 1px solid #dedede;
  border-radius: 3px;
  margin-top: 40px;
}

.footer {
  margin-bottom: 10px;
}

.footer > a {
  margin: 0em 5px;
  font-size: 1.5em;
}

.content {
  padding-left: 10%;
  padding-right: 10%;
  text-align: left;
}



